.price__list-slider {
  @include list-reset;
  position: relative;
  padding-bottom: 30px;
}

.price__item-slaider {
  display: flex;
  justify-content: center;
}

.price__item-slaider--left {
  position: relative;
  left: 50%;
  top: 1px;
  transform: translate(-100%, 0);
  width: 50%;
}

.price__item-slaider--right {
  position: absolute;
  left: 50%;
  transform: translate(0, 0);
  top: 1px;
  width: 50%;
}

.price__wrapper-slaider {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 266px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    height: 522px;
  }
}

.price__item-slaider img {
  position: absolute;
  bottom: 12px;
  transform: translate(-50%, 0);
}

.price__item-slaider--left img {
  left: 100%;
  width: 280px;
  height: 256px;

  @media (min-width: $tablet-width) {
    width: 560px;
    height: 512px;
  }
}

.price__item-slaider--right img {
  left: 0;
  width: 280px;
  height: 256px;

  @media (min-width: $tablet-width) {
    width: 560px;
    height: 512px;
  }
}

.price__item-wrapper {
  position: relative;
}

.price__item-wrapper--line::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
  width: 4px;
  height: 256px;
  background-color: $text-color_6;
  border-radius: 2px;
  bottom: 0;
  left: 50%;
  z-index: 5;

  @media (min-width: $tablet-width) {
    height: 512px;
  }
}

.price__item-toggle {
  mask-image: url("../img/range-toggle.svg");
  background-color: $text-color_6;
  position: absolute;
  width: 24px;
  height: 18px;
  top: 10px;
  left: 8px;
  border: none;
  z-index: 6;
}

.price__item-wrapper--line:hover::before,
.price__item-toggle:hover {
  background-color: $btn-bg-color;
}

.price__item-wrapper--line:active::before,
.price__item-toggle:hover {
  background-color: $btn-bg-color;
  opacity: 0.6;
}

.price__item-wrapper-toogle {
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $bg-color;
  border: 2px solid $text-color_6;
  bottom: 110px;
  left: calc(50% - 19px);
  z-index: 6;

  @media (min-width: $tablet-width) {
    bottom: 200px;
  }

  @media (min-width: $desktop-width) {
  }
}

.price__item-wrapper-toogle:hover {
  border: 2px solid $btn-bg-color;
}

.price__item-wrapper-toogle:active {
  border: 2px solid $btn-bg-color;
  opacity: 0.6;
}
